<template>
  <el-container>
    <el-header class="xm-header">
      <span class="xm-header-title">
        <i class="el-icon-more i-more" :class="collapseClass" @click="isCollapse=!isCollapse"></i>
        <span class="xm-home-title" @click="$router.push('/home')" style="margin-left: 10px">xmBot</span>
      </span>
      <el-dropdown class="float-right" v-if="isLogin">
        <span class="xm-home-title">{{homeTitle}}<i class="el-icon-arrow-down el-icon--right"></i></span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item @click.native="handleLogout(true)">切换用户</el-dropdown-item>
          <el-dropdown-item @click.native="handleLogout(false)">注销登录</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <span v-else class="float-right xm-home-title" @click="$router.push('/login')">{{homeTitle}}</span>
    </el-header>
    <el-container>
      <el-aside style="width: auto">
        <el-menu router :collapse="isCollapse" :default-active="activeMenu">
          <el-menu-item v-for="(item,key) of menu" :key="key" :index="item.path" :disabled="!isLogin&&item.meta&&item.meta.auth">
            <i :class="item.icon&&item.icon!==''?item.icon:'el-icon-menu'"></i>
            <span slot="title">{{item.title}}</span>
          </el-menu-item>
        </el-menu>
      </el-aside>
      <el-main style="padding-top: 5px !important;" class="div-main">
        <keep-alive>
          <router-view/>
        </keep-alive>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
  import {menuDict} from '../router/index'

  export default {
    name: "Index",
    data() {
      return {
        user_id: '',
        isCollapse: false,
        menu: menuDict,
        activeMenu:'/home'
      }
    },
    beforeRouteUpdate(to,from,next){
      this.flushRoute(to.fullPath)
      next()
    },
    watch:{

    },
    computed: {
      isLogin() {
        return this.user_id && this.user_id !== ''
      },
      homeTitle() {
        if (this.isLogin) {
          return `${this.user_id}`
        } else {
          return '未登录'
        }
      },
      collapseClass() {
        return this.isCollapse ? 'el-icon-more i-more' : 'el-icon-more translateY-90 i-more'
      }
    },
    methods: {
      handleLogout(toLogin) {
        this.$confirm(`是否${toLogin?'切换用户':'注销登录'}`, '确认',{
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(()=>{
          this.$cookies.remove('token')
          this.$cookies.remove('user_id')
          this.$cookies.remove('salt')
          this.$cookies.remove('login_count')
          if (toLogin) {
            this.$router.push('/login')
          }else{
            window.location.reload()
          }
        }).catch(()=>{})
      },
      flushRoute(fullPath){
        const path = fullPath?fullPath:this.$route.fullPath
        let flag = this.menu.some(o=>{
          if (o.path === path) {
            this.activeMenu = path
            return true
          }
          return false
        })
        if (!flag) this. activeMenu = ''
      }
    },
    created() {
      this.flushRoute()
    },
    mounted() {
      let user_id = this.$cookies.get('user_id')
      if (user_id) {
        this.user_id = user_id
      }
    }
  }
</script>

<style scoped>
  .div-main{
    max-height: 860px;
    overflow: auto;
  }

  .el-row {
    margin-bottom: 10px;
  }

  .xm-header-title {
    font-size: 16px;
    font-weight: bold;
  }

  .xm-header {
    height: 60px;
    line-height: 60px;
    box-shadow: 0 1px 3px 2px #67676780;
    margin-bottom: 5px;
  }

  .xm-home-title {
    text-decoration: underline;
    cursor: pointer;
    margin-right: 10px;
  }

  .xm-home-title:hover {
    color: #8c939d;
  }

  .translateY-90 {
    transform: rotate(90deg);
  }

  .i-more{
    font-size: 16px;
    cursor:pointer;
  }

  .i-more:hover{
    color: #66b1ff;
  }
</style>



